.userCard{
	width: 180px;
	height: 300px;
	background-color: #fff;
	border: 1px #cdcdcd solid;
	border-radius: 13px;
	margin: 5px ;
	box-shadow: 2px 2px 4px rgba(142, 142, 141, 0.5);
}
.userPhoto{
	width: 178px;
	height: 178px;
	/* margin: 0 0 4px 0; */
	border-radius: 13px 13px 0 0;
}
userCardName{
	font-size: 16px;
	font-weight: 700;
	color: rgba(26, 138, 207);
}
.userCardText{
	padding: 3px 5px 10px 7px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 94px;
}
.userCardStatus{
	align-self: start;
}


